import Vue from 'vue'
// 
// 
import {
    // 
    Tabbar,
    TabbarItem,
    // 
    Icon,
    // 
    Search,
    // layout
    Row,
    Col,
    // 
    Button,
    // Sticky 
    Sticky,
    // 
    Swipe,
    SwipeItem,
    // 
    Grid,
    GridItem,
    // 
    Image,
    // 
    NavBar,
    // 
    Tab,
    Tabs,
    // 
    DropdownMenu,
    DropdownItem,
    // 
    Card,
    // 
    Stepper,
    // 
    GoodsAction,
    GoodsActionIcon,
    GoodsActionButton,
    // 
    Divider,
    // 
    Dialog,
    // 
    Sidebar,
    SidebarItem,
    // 
    Field,
    // 
    Slider,
    // 
    RadioGroup,
    Radio,
    // 
    Checkbox,
    CheckboxGroup,
    // 
    PasswordInput,
    NumberKeyboard,
    // 
    CountDown
} from 'vant';

export default () => {
    Vue.use(Tabbar).use(TabbarItem)
        .use(Icon)
        .use(Search)
        .use(Row).use(Col)
        .use(Button)
        .use(Sticky)
        .use(Swipe).use(SwipeItem)
        .use(Grid).use(GridItem)
        .use(Image)
        .use(NavBar)
        .use(Tab).use(Tabs)
        .use(DropdownMenu).use(DropdownItem)
        .use(Card)
        .use(Stepper)
        .use(GoodsAction).use(GoodsActionIcon).use(GoodsActionButton)
        .use(Divider)
        .use(Dialog)
        .use(Sidebar).use(SidebarItem)
        .use(Field)
        .use(Slider)
        .use(Radio).use(RadioGroup)
        .use(Checkbox).use(CheckboxGroup)
        .use(PasswordInput).use(NumberKeyboard)
        .use(CountDown);
}